import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export interface Product {
  id: string;
  title: string;
  description: string;
  image: string;
  isLiked: boolean;
}

interface ProductState {
  products: Product[];
  filteredProducts: Product[];
  favoritesOnly: boolean;
}

// Функция для загрузки состояния из localStorage
const loadState = (): ProductState => {
  const savedState = localStorage.getItem('productsState');
  if (savedState) {
    const parsedState = JSON.parse(savedState);
    console.log('Loaded state from localStorage:', parsedState);  // Логируем загруженное состояние
    return parsedState;
  }
  return { products: [], filteredProducts: [], favoritesOnly: false };
};




// Функция для сохранения состояния в localStorage
const saveState = (state: ProductState) => {
  console.log('Saving state to localStorage:', state);  // Логируем сохраняемое состояние
  localStorage.setItem('productsState', JSON.stringify(state));
};



const initialState: ProductState = loadState();

const productSlice = createSlice({
  name: 'products',
  initialState,
  reducers: {
    setProducts(state, action: PayloadAction<Product[]>) {
      console.log('Setting products:', action.payload);  // Логируем данные, которые приходят в action
      state.products = action.payload;
      state.filteredProducts = action.payload;
      saveState(state);  // Сохраняем актуальное состояние
    }
    ,
    toggleLike(state, action: PayloadAction<string>) {
      const product = state.products.find((p) => p.id === action.payload);
      if (product) product.isLiked = !product.isLiked;
      state.filteredProducts = state.products;
      saveState(state);
    },
    deleteProduct(state, action: PayloadAction<string>) {
      console.log('Before deletion:', state.products);  // Логируем перед удалением
      state.products = state.products.filter((p) => p.id !== action.payload);
      state.filteredProducts = state.filteredProducts.filter((p) => p.id !== action.payload); // Обновляем filteredProducts
      console.log('After deletion:', state.products);   // Логируем после удаления
      saveState(state); // Сохраняем актуальное состояние
    },
    filterFavorites(state) {
      state.favoritesOnly = !state.favoritesOnly;
      state.filteredProducts = state.favoritesOnly
        ? state.products.filter((p) => p.isLiked)
        : state.products;
      saveState(state);
    },
    addProduct(state, action: PayloadAction<Product>) {
      state.products.push(action.payload);
      state.filteredProducts = state.products;
      saveState(state); // Сохраняем состояние в localStorage
    },     
    updateProduct(state, action: PayloadAction<Product>) {
      const index = state.products.findIndex((p) => p.id === action.payload.id);
      if (index !== -1) state.products[index] = action.payload;
      state.filteredProducts = state.products;
      saveState(state);
    },
  },
});

export const {
  setProducts,
  toggleLike,
  deleteProduct,
  filterFavorites,
  addProduct,
  updateProduct,
} = productSlice.actions;

export default productSlice.reducer;
